@charset "UTF-8";
@font-face {
  font-family: Roboto;
  font-display: swap;
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  font-display: swap;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}
:root {
  --font-family: "Roboto", sans-serif;
  --main-color: #000000;
  --gray-color: #6b6b6b;
}

/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  min-width: 320px;
  font-size: 100%;
}

body {
  color: var(--main-color);
  line-height: 1;
  font-size: 1rem;
  font-family: var(--font-family);
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: var(--font-family);
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*--------------------*/
.wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100%;
}

.page {
  flex: 1 1 auto;
}

[class*=__container] {
  max-width: 79.375rem;
  box-sizing: content-box;
  padding: 0 0.9375rem;
  margin: 0 auto;
}

.title {
  font-size: 2.5rem;
  line-height: 1.2;
  font-weight: 700;
}

.text {
  font-size: 1.125rem;
  line-height: 1.3333333333;
  color: var(--gray-color);
  text-wrap: balance;
}
.text p:not(:last-child) {
  margin-bottom: 1.8em;
}
@media (max-width: 29.99875em) {
  .text {
    font-size: 0.875rem;
  }
}

.spollers__item._active .spollers__title::after {
  transform: rotate(90deg);
  opacity: 0;
}
.spollers__title {
  text-wrap: balance;
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  text-align: left;
  padding: 0.9375rem;
  padding-right: 3.125rem;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1.1666666667;
}
.spollers__title::after {
  content: "";
  position: absolute;
  right: 30px;
  width: 0.0625rem;
  height: 0.9375rem;
  background-color: var(--main-color);
  transition: transform 0.3s, opacity 0.3s;
}
.spollers__title::before {
  content: "";
  position: absolute;
  right: 30px;
  width: 0.0625rem;
  height: 0.9375rem;
  background-color: var(--main-color);
  transform: rotate(90deg);
}
@media (max-width: 29.99875em) {
  .spollers__title {
    font-size: 1.125rem;
  }
}
.spollers__body {
  padding: 0.625rem 0.9375rem;
}

.cart {
  position: absolute;
  width: 3.75rem;
  right: 15px;
  z-index: 50;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.cart._active {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}
.cart._fixed {
  position: fixed;
}
.cart__count {
  position: absolute;
  width: 1.25rem;
  height: 1.25rem;
  right: 0;
  top: 0;
  background-color: red;
  color: #ffffff;
  font-size: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.tabs__navigation {
  display: flex;
  justify-content: center;
}
.tabs__navigation:not(:last-child) {
  margin-bottom: 1.25rem;
}
.tabs__title {
  padding: 1.25rem;
  font-size: 1.125rem;
  line-height: 1.1111111111;
  font-weight: 500;
  transition: all 0.3s ease 0s;
}
.tabs__title._active {
  pointer-events: none;
  background-color: var(--main-color);
  color: #ffffff;
}
.tabs__title:first-child {
  border-radius: 0.625rem 0rem 0rem 0rem;
}
.tabs__title:last-child {
  border-radius: 0rem 0.625rem 0rem 0rem;
}
@media (any-hover: hover) {
  .tabs__title:hover {
    background-color: var(--main-color);
    color: #ffffff;
  }
}
@media (max-width: 35em) {
  .tabs__title {
    font-size: 1rem;
    padding: 0.9375rem;
  }
}
@media (max-width: 29.99875em) {
  .tabs__title {
    font-size: 0.75rem;
    padding: 0.625rem;
  }
}
.tabs__body {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(25rem, 1fr));
  gap: 1.875rem;
  overflow: hidden;
  height: 100%;
  opacity: 1;
  transition: opacity 0.8s;
}
@media (max-width: 61.99875em) {
  .tabs__body {
    grid-template-columns: repeat(auto-fill, minmax(18.125rem, 1fr));
  }
}
.tabs__filter {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1.875rem;
  padding-block: 1.25rem;
}
.tabs__filter:not(:last-child) {
  margin-bottom: 1.25rem;
}
@media (max-width: 61.99875em) {
  .tabs__filter {
    flex-direction: column;
  }
}
@media (max-width: 34.8125em) {
  .tabs__slider {
    margin-bottom: 0.625rem;
  }
}

.slider-tabs {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.25rem 0.625rem;
}
.slider-tabs__body {
  position: relative;
  background-color: #ddd;
  color: #a16241;
  width: 20.625rem;
  height: 0.5rem;
  border-radius: 0.1875rem;
  cursor: pointer;
  touch-action: none;
  user-select: none;
}
@media (min-width: 29.99875em) {
  .slider-tabs__body {
    margin-inline: 2.5rem;
  }
  .slider-tabs__body::after {
    content: "";
    position: absolute;
    width: 2.5rem;
    height: 1.25rem;
    right: -50px;
    top: 50%;
    transform: translateY(-50%);
    background: url("../img/svgsprite/stack/svg/sprite.stack.svg#five-stars") center/65% no-repeat;
  }
  .slider-tabs__body::before {
    content: "";
    position: absolute;
    width: 2.5rem;
    height: 1.25rem;
    left: -45px;
    top: 50%;
    transform: translateY(-50%);
    background: url("../img/svgsprite/stack/svg/sprite.stack.svg#star") center/30% no-repeat;
  }
}
@media (max-width: 22.5em) {
  .slider-tabs__body {
    width: 100%;
  }
}
.slider-tabs__thumb {
  background-color: #ffffff;
  border-radius: 0.1875rem;
  width: 1.25rem;
  height: 1.25rem;
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  margin-left: -0.625rem;
  transform: translate(0, -50%);
  border: 1px solid #000;
  cursor: grab;
}
.slider-tabs__value {
  color: var(--main-color);
  font-size: 0.75rem;
  font-weight: 700;
  position: absolute;
  left: 0;
  top: calc(100% + 8px);
  text-align: center;
  width: 100%;
  pointer-events: none;
  cursor: default;
}
.slider-tabs__progress {
  height: 0.5rem;
  border-radius: 0.1875rem;
  position: absolute;
  width: 50%;
  top: 50%;
  left: 0;
  right: 0;
  z-index: 1;
  background: linear-gradient(90deg, #ffca98 0%, #a16241 52%, #1f0e04 100%);
  transform: translate(0, -50%);
}
.slider-tabs__steps {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  position: absolute;
  top: calc(100% - 2px);
  left: 0;
  right: 0;
}
.slider-tabs__steps span {
  background-color: #ddd;
  display: inline-flex;
  width: 2px;
  height: 9px;
  transition: 0.2s height;
}
.slider-tabs__steps span:not(:first-child) {
  margin-left: -1px;
}
.slider-tabs__steps span._active {
  background-color: var(--main-color);
  height: 0.875rem;
}

.checkbox__input {
  display: none;
}
.checkbox__input:checked ~ .checkbox__label::after {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: scale(1);
}
.checkbox__label {
  position: relative;
  display: flex;
  align-items: center;
  gap: 0.3125rem;
  cursor: pointer;
}
.checkbox__label::before {
  content: "";
  width: 1rem;
  height: 1rem;
  border: 1px solid var(--main-color);
}
.checkbox__label::after {
  content: "";
  position: absolute;
  z-index: 2;
  left: 4px;
  top: 4px;
  width: 0.5rem;
  height: 0.5rem;
  background-color: var(--main-color);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: scale(0);
  transition: all 0.3s ease 0s;
}
.card__image {
  aspect-ratio: 1200/800;
}
.card__image:not(:last-child) {
  margin-bottom: 0.9375rem;
}
.card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card__title {
  font-size: 1.125rem;
  font-weight: 500;
}
.card__title:not(:last-child) {
  margin-bottom: 0.9375rem;
}
.card__info {
  font-size: 0.875rem;
}
.card__info:not(:last-child) {
  margin-bottom: 0.625rem;
}
.card__btn {
  position: relative;
  display: block;
  width: 100%;
  font-size: 1rem;
  background-color: transparent;
  border-bottom: 1px solid #000;
  padding: 0.625rem;
  overflow: hidden;
}
.card__btn span {
  position: relative;
  z-index: 2;
  transition: all 0.3s ease 0s;
}
.card__btn::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: var(--main-color);
  transform: translateY(100%);
  opacity: 0;
  transition: all 0.3s ease 0s;
}
@media (any-hover: hover) {
  .card__btn:hover::after {
    opacity: 1;
    transform: translateY(0%);
  }
  .card__btn:hover span {
    color: #ffffff;
  }
}

.slider {
  display: grid;
  grid-template-columns: 3.125rem auto 3.125rem;
  gap: 0.625rem;
}
@media (max-width: 47.99875em) {
  .slider {
    grid-template-columns: 1.25rem auto 1.25rem;
  }
}
.slider__arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  transition: opacity 0.3s ease 0s;
}
@media (any-hover: hover) {
  .slider__arrow:hover {
    opacity: 0.2;
  }
}
.slider__arrow--next {
  transform: rotate(180deg);
}
.slider__block {
  overflow: hidden;
}
.slider__items {
  width: 100%;
  height: 100%;
  display: flex;
  transition: transform 0.5s ease 0s;
}
.slider__item {
  flex: 0 0 31.9%;
  display: flex;
  flex-direction: column;
  height: 100%;
  margin-right: 1.25rem;
}
@media (max-width: 81.25em) {
  .slider__item {
    flex: 0 0 49.6%;
  }
}
@media (max-width: 47.99875em) {
  .slider__item {
    flex: 0 0 100%;
  }
}
.slider__text {
  flex: 1 1 100%;
}
.slider__text:not(:last-child) {
  margin-bottom: 1.25rem;
}
.slider__name {
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1.1666666667;
}
@media (max-width: 29.99875em) {
  .slider__name {
    font-size: 1.125rem;
  }
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.85);
  transition: all 0.5s ease 0s;
}
.popup._active {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
}
.popup__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100%;
  padding: 1.875rem 0.9375rem;
}
.popup__content {
  position: relative;
  background-color: #ffffff;
  max-width: 56.25rem;
  padding: 2.5rem 1.25rem 1.25rem;
  border-radius: 0.9375rem;
  width: 100%;
}
.popup__close {
  position: absolute;
  right: 15px;
  top: 15px;
  background-color: transparent;
  display: block;
  width: 1.5625rem;
  height: 1.8125rem;
  cursor: pointer;
  z-index: 5;
}
.popup__close span, .popup__close::before, .popup__close::after {
  content: "";
  transition: all 0.3s ease 0s;
  right: 0;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: var(--gray-color);
}
.popup__close::before {
  top: calc(50% - 1px);
  transform: rotate(-45deg);
}
.popup__close::after {
  bottom: calc(50% - 1px);
  transform: rotate(45deg);
}
@media (any-hover: hover) {
  .popup__close:hover::before, .popup__close:hover::after {
    background-color: red;
  }
}
.popup__title {
  text-align: center;
}
.popup__title:not(:last-child) {
  margin-bottom: 1.25rem;
}
.popup__items {
  display: grid;
  gap: 1.25rem;
}
.popup__items:not(:last-child) {
  margin-bottom: 1.25rem;
}
.item-card {
  display: flex;
  align-items: center;
  gap: 1.25rem;
}
@media (max-width: 29.99875em) {
  .item-card {
    flex-wrap: wrap;
  }
}
@media (min-width: 29.99875em) {
  .item-card__image {
    max-width: 11.25rem;
  }
}
.item-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.item-card__body {
  display: flex;
  align-items: center;
  flex: 1 1 100%;
  gap: 1.25rem;
}
@media (max-width: 47.99875em) {
  .item-card__body {
    flex-direction: column;
    align-items: flex-start;
  }
}
.item-card__info {
  flex: 1 1 100%;
}
.item-card__title:not(:last-child) {
  margin-bottom: 0.625rem;
}
.item-card__group {
  display: flex;
  align-items: center;
  gap: 2.5rem;
}
.item-card__quantity {
  flex: 0 0 8.4375rem;
}
.item-card__price {
  text-align: right;
  flex: 0 0 5rem;
}

.quantity {
  display: flex;
  align-items: center;
  gap: 1.25rem;
}
.quantity__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.1875rem;
  height: 2.1875rem;
  font-size: 1.625rem;
  flex: 0 0 2.1875rem;
  background-color: #ddd;
  border-radius: 50%;
  cursor: pointer;
  transition: opacity 0.3s ease 0s;
}
@media (any-hover: hover) {
  .quantity__btn:hover {
    opacity: 0.6;
  }
}
.quantity__count {
  text-align: center;
  flex: 0 0 1.5625rem;
}

.form-popup__body {
  display: grid;
  gap: 0.9375rem;
}
.form-popup__body:not(:last-child) {
  margin-bottom: 1.25rem;
}
@media (min-width: 47.99875em) {
  .form-popup__body {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
}
@media (min-width: 47.99875em) {
  .form-popup__input:last-child {
    grid-column: span 3;
  }
}
.form-popup__group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1.25rem;
}
@media (max-width: 29.99875em) {
  .form-popup__group {
    flex-direction: column;
    justify-content: center;
  }
}
.form-popup__total-price {
  font-size: 1.25rem;
}
.form-popup__total-price span {
  font-weight: 700;
}
.form-popup__btn {
  background-color: var(--main-color);
  color: #ffffff;
  padding: 0.9375rem 1.25rem;
  font-size: 1.25rem;
  transition: opacity 0.3s ease 0s;
}
@media (any-hover: hover) {
  .form-popup__btn:hover {
    opacity: 0.7;
  }
}
@media (max-width: 29.99875em) {
  .form-popup__btn {
    width: 100%;
  }
}

.input {
  width: 100%;
  border: 1px solid var(--main-color);
  padding: 0.625rem;
  font-size: 1.25rem;
  color: var(--main-color);
}
.input::placeholder {
  color: var(--gray-color);
  opacity: 0.6;
  transition: opacity 0.3s ease 0s;
}
.input:focus::placeholder {
  opacity: 0;
}

.icon {
  fill: transparent;
  stroke: transparent;
  width: 1.25rem;
  height: 1.25rem;
  transition: all 0.3s ease 0s;
}

.icon--arrow {
  fill: var(--main-color);
}
@media (any-hover: hover) {
  .icon--arrow:hover {
    opacity: 0.8;
  }
}

.icon--cart {
  stroke: var(--main-color);
  fill: #fff;
  width: 3.75rem;
  height: 3.75rem;
}

.header {
  padding-block: 0.625rem;
}
.header__container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__logo {
  width: 12.5rem;
  height: 12.5rem;
}
.header__logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.products {
  padding-block: 6.25rem;
}
@media (max-width: 61.99875em) {
  .products {
    padding-block: 3.125rem;
  }
}
@media (max-width: 29.99875em) {
  .products {
    padding-block: 1.5625rem;
  }
}
.products__title:not(:last-child) {
  margin-bottom: 2.5rem;
}
@media (max-width: 47.99875em) {
  .products__title:not(:last-child) {
    margin-bottom: 1.25rem;
  }
}
.reviews {
  padding-block: 6.25rem;
}
@media (max-width: 61.99875em) {
  .reviews {
    padding-block: 3.125rem;
  }
}
@media (max-width: 29.99875em) {
  .reviews {
    padding-block: 1.5625rem;
  }
}
.reviews__title:not(:last-child) {
  margin-bottom: 2.5rem;
}
@media (max-width: 47.99875em) {
  .reviews__title:not(:last-child) {
    margin-bottom: 1.25rem;
  }
}
.faq {
  padding-block: 6.25rem;
}
@media (max-width: 61.99875em) {
  .faq {
    padding-block: 3.125rem;
  }
}
@media (max-width: 29.99875em) {
  .faq {
    padding-block: 1.5625rem;
  }
}
.faq__title:not(:last-child) {
  margin-bottom: 2.5rem;
}
@media (max-width: 47.99875em) {
  .faq__title:not(:last-child) {
    margin-bottom: 1.25rem;
  }
}
.faq__body {
  display: grid;
  gap: 1.875rem;
}
@media (max-width: 47.99875em) {
  .faq__body {
    gap: 0.9375rem;
  }
}